import {MacAgora} from "./macAgora";
import {getRoleType} from "./util";
import {TENCENT_APP_ID, TENCENT_APP_TYPE} from "./constants";
import {macNativeMethod} from "zmNativeBridge";

export class MacTencentOne extends MacAgora {
    constructor(options) {
        super(options)
        const {tencentSign} = options;
        this.rtcName = 'mac tencent1';
        this.loggerColor = 'cornflowerblue';
        this.tencentSign = tencentSign;
        this._onTencentRoomCreate_ = this._onTencentRoomCreate_.bind(this);
        this._onTencentJoinRoomSucc_ = this._onTencentJoinRoomSucc_.bind(this);
        this.tencentRoomCreate = this.tencentRoomCreate.bind(this);
        this.pushTencentStream = this.pushTencentStream.bind(this);
        this.createRoom = this.createRoom.bind(this);
    }
    init(){
        super.init()
        this._isFirstLocalFrameInited_ = true;
    }
    _onTencentRoomCreate_(data){
        this.rtcCb({name:'tencent room create',data})
    }
    _onTencentJoinRoomSucc_(data){
        this.rtcCb({name:'tencent join room succ',data:{}})
    }
    tencentRoomCreate(data){
        macNativeMethod(['joinRoom', data.roomId, 'rolename', 'LiveMaster']);
    }
    createRoom(roomId){
        macNativeMethod(['createRoom', roomId]);
    }
    pushTencentStream(){
        macNativeMethod(['startPushStream'])
    }
    _initModule_(){
        const watcherType = getRoleType(this.role, this.isWatcher)
        macNativeMethod(['initModule', 'getNativeWin-classroomwin',
            'appid', TENCENT_APP_ID, 'apptype', TENCENT_APP_TYPE,
            'roomid', this.roomId, 'uid', `${this.uid}`, 'password', '',
            'sig', this.tencentSign, 'watcherflag', watcherType]);

        this.rtcCb({
            name: 'onFirstLocalVideoFrame',
            data: {
                code: this._code_enum_.MESSAGE_CODE_STREAM,
            }
        })      
    }

}
