function getZmlearnPlugin() {
    if (getClientVersion() >= '2.4.21') {
        var path = window.require("path");
        return getRemote().require(path.join(getRemote().app.getAppPath(), '../../zmlearn'));
    } else {
        return getRemote().require('./zmlearn');
    }
}

