import {makeChannelInstance} from './channelFactory'
import {makeUid} from './util'

export function init(options) {
    const {
        channel,
        kind,
        avsEngine,
        ZmAvsSDK,
        classType,
    } = options
    const RtcClass = makeChannelInstance(channel,kind);
    const rtc = new RtcClass(options);
    rtc.init(avsEngine,ZmAvsSDK,classType);
    return rtc
}

export function makeUidFromMobile(mobile){
    return makeUid(mobile)
}