import {WinAgora} from "./winAgora";
import {getCurrentWin, macInitModule, macNativeMethod, macSendWinHandle, nativeOff, nativeOn} from "zmNativeBridge";
import {APP_ID} from "./constants";
import {getRoleType} from "./util";

export class MacAgora extends WinAgora {
    constructor(options) {
        super(options)
        this.rtcName = 'mac agora';
        this.loggerColor = 'lawngreen';
        this.isSupportBeauty = false;

    }

    _initModule_(){
        const cWin = getCurrentWin();
        const myView = cWin.getNativeWindowHandle();
        macSendWinHandle(myView);
        macInitModule(this.channel);

    }

    _remoteJoin_(data) {
        const uid = data.data
        this._updateUids_(uid);
        this._moveFunc_();
    }

    init() {
        super.init()
    }

    _moveById_(id) {
        id = `${id}`
        const {width, height} = getCurrentWin().getContentBounds();
        const size = [width, height];
        const dom = this.document.getElementById(id);
        if (dom) {
            const bound = dom.getBoundingClientRect()
            const {left,top,height,width} = bound;
            const x1 = parseInt(left);
            const y1 = parseInt(size[1] - top - height);
            const w = parseInt(width);
            const h = parseInt(height);
            this.logger(`macwin move ${x1},${y1}, ${w}, ${h}, ${id}`);
            macNativeMethod(['changeWinViewById', id,
                'x', x1,
                'y', y1, 'w', w, 'h', h]);
        }
    }

    joinRoom() {
        const watcherType = getRoleType(this.role, this.isWatcher)
        macNativeMethod(['initModule', 'getNativeWin-classroomwin', 'appid', APP_ID, 'roomid',
            this.roomId, 'uid', this.uid, 'watcherflag', watcherType]);
        macNativeMethod(['joinRoom', this.roomId, 'uid', this.uid]);
        macNativeMethod(['changeWinViewById', this.uid,
            'x', 0,
            'y', 0, 'w', 0, 'h', 0]);
    }

    leaveRoom() {
        macNativeMethod(['LeaveRoom']);
    }

    playById(id) {

    }

    disableVideo({id, flag}) {
        if(`${id}` === `${this.uid}`){
            macNativeMethod(['muteVideo', flag]);
        }else{
            macNativeMethod(['muteVideoById', id, 'flag',flag]);
        }
    }

    disableAudio({id, flag}) {
        if(`${id}` === `${this.uid}`){
            macNativeMethod(['muteVoice', flag]);
        }else{
            macNativeMethod(['muteVoiceById', id, 'flag',flag]);
        }
    }

    hideVideoById(id) {
        id = `${id}`
        macNativeMethod(['changeWinViewById', id,
            'x', 0,
            'y', 0, 'w', 0, 'h', 0]);
        this._updateHidderUids_(id, 'add')
    }

    showVideoById(id) {
        super.showVideoById(id);
    }


}