import {RtcKit} from "./rtcKit";
import {showErrorMsg, waitFor} from "./util";
import {APP_ID, MESSAGE_CODE_UNKONOWN} from "./constants";

const AgoraRTC = require('./libs/AgoraRTCSDK-2.8.0');
let client = null;
let streams = {};

//AgoraRTC.Logger.disableLogUpload();

export class WebAgora extends RtcKit {
    constructor(options) {
        super(options)
        this.rtcName = 'web agora';
        this.loggerColor = 'green';
        this.timeId = null;

    }

    init() {
        if (!AgoraRTC.checkSystemRequirements()) {
            this.rtcCb({
                name: 'error',
                data: {
                    msg: 'WebRTC',
                    code: this._code_enum_.MESSAGE_CODE_INIT,
                }
            })
            return;
        }
        if (client) {
            this.rtcCb({
                name: 'error',
                data: {
                    msg: '',
                    code: this._code_enum_.MESSAGE_CODE_INIT,
                }
            })
            return;
        }

        const localCid = localStorage.getItem('ss.media.video_source_id');
        const localMid = localStorage.getItem('ss.media.audio_source_id');
        AgoraRTC.getDevices((devices) => {
            if (devices) {
                this.logger('getDevices',devices);
                for (var i = 0; i < devices.length; i++) {
                    if (devices[i].deviceId === localCid) this.cameraId = localCid;
                    if (devices[i].deviceId === localMid) this.microphoneId = localMid;
                }
            }

            this.logger('cameraId,microphoneId:', this.cameraId, this.microphoneId);
            client = AgoraRTC.createClient({mode: "live", codec: "vp8"});

            client.on('stream-published', (evt) => {
                this.logger("");
                this.rtcCb({
                    name: 'stream-published',
                    data: {
                        code: this._code_enum_.MESSAGE_CODE_STREAM,
                    }
                })
            });

            client.on('error', (err) => {
                this.logger("Got error msg:", err.reason);
                this.rtcCb({
                    name: 'error',
                    data: {
                        msg: showErrorMsg(err),
                        code: this._code_enum_.MESSAGE_CODE_UNKONOWN,
                    }
                })
            });


            client.on('stream-added', (evt) => {
                const stream = evt.stream;
                this.logger(`: ${stream.getId()}`);
                this.logger(`: ${stream.getId()}`);
                this.rtcCb({
                    name: 'stream-added',
                    data: {
                        uid: stream.getId(),
                        code: this._code_enum_.MESSAGE_CODE_STREAM,
                    }
                })
                client.subscribe(stream, (err) => {
                    const msg = `: ${showErrorMsg(err)}`;
                    this.logger(msg);
                    this.rtcCb({
                        name: 'error',
                        data: {
                            msg,
                            code: this._code_enum_.MESSAGE_CODE_STREAM,
                        }
                    })
                });
            });

            client.on('stream-subscribed', (evt) => {
                const stream = evt.stream;
                const id = stream.getId();
                this.logger(`: ${id}`);
                streams[id] = stream;
                this.playById(id);
                this.rtcCb({
                    name: 'stream-subscribed',
                    data: {
                        uid: id,
                        code: this._code_enum_.MESSAGE_CODE_STREAM,
                    }
                })
                this.rtcCb({
                    name: 'RemoteJoin',
                    data: {
                        uid:id,
                        code: this._code_enum_.MESSAGE_CODE_STREAM,
                    }
                })
            });

            client.on('stream-removed', (evt) => {
                const stream = evt.stream;
                stream.stop();
                delete streams[stream.getId()];
                this.logger(` ${stream.getId()}`);
                this.rtcCb({
                    name: 'stream-removed',
                    data: {
                        uid: stream.getId(),
                        code: this._code_enum_.MESSAGE_CODE_STREAM,
                    }
                })
            });

            client.on('peer-leave', (evt) => {
                const stream = evt.stream;
                if (stream) {
                    stream.stop();
                    delete streams[evt.uid];
                    this.logger(`${evt.uid} `);
                    this.rtcCb({
                        name: 'peer-leave',
                        data: {
                            uid: evt.uid,
                            code: this._code_enum_.MESSAGE_CODE_ROOM,
                        }
                    })
                }
            });
            client.on('network-quality', ({downlinkNetworkQuality,uplinkNetworkQuality})=>{
                //this.logger('network-quality',a,b);
                this.rtcCb({
                    name: 'NetQA',
                    data: {
                        rxQuality:downlinkNetworkQuality,
                        txQuality:uplinkNetworkQuality,
                        code: this._code_enum_.MESSAGE_CODE_STREAM,
                    }
                })
            })

            client.on("mute-audio", (evt) => {
                const uid = evt.uid;
                this.rtcCb({
                    name: "mute_remote_audio",
                    data: {
                        uid,
                        mute:true,
                        code: this._code_enum_.MESSAGE_CODE_STREAM,
                    }
                })

            });

            client.on("unmute-audio", (evt) => {
                const uid = evt.uid;
                this.rtcCb({
                    name: "mute_remote_audio",
                    data: {
                        uid,
                        mute:false,
                        code: this._code_enum_.MESSAGE_CODE_STREAM,
                    }
                })

            });

            client.on("mute-video", (evt) => {
                const uid = evt.uid;
                this.rtcCb({
                    name: "mute_remote_video",
                    data: {
                        uid,
                        mute:true,
                        code: this._code_enum_.MESSAGE_CODE_STREAM,
                    }
                })

            });

            client.on("unmute-video", (evt) => {
                const uid = evt.uid;
                this.rtcCb({
                    name: "mute_remote_video",
                    data: {
                        uid,
                        mute:false,
                        code: this._code_enum_.MESSAGE_CODE_STREAM,
                    }
                })

            });


            client.init(APP_ID, () => {
                this.logger('');
                this.rtcCb({
                    name: 'inited',
                    data: {
                        code: this._code_enum_.MESSAGE_CODE_INIT,
                    }
                })
                this.joinRoom()
                this._getVideoStatus_();

            }, (err) => {
                const msg = `: ${showErrorMsg(err)}`
                this.logger(msg);
                this.rtcCb({
                    name: 'error',
                    data: {
                        msg,
                        code: this._code_enum_.MESSAGE_CODE_INIT,
                    }
                })
            });


        });

    }

    _getVideoStatus_(){
        if(this.timeId){
            clearInterval(this.timeId)
            this.timeId = null;
        }
        this.timeId = setInterval(()=>{
            this._getLocalVideoStats();
            this._getRemoteVideoStats();
        },3000)
    }

    joinRoom() {
        const userId = this.uid;
        const roomId = this.roomId;
        const isWatcher = this.isWatcher;
        this.logger(`:${userId} isWatcher:${!!isWatcher} :${roomId}...`);
        client.join(null, roomId, userId, (uid) => {
            this.logger(` ${uid} `);
            this.rtcCb({
                name: 'room joined',
                data: {
                    code: this._code_enum_.MESSAGE_CODE_ROOM,
                }
            })

            if (!isWatcher) {
                const localStream = AgoraRTC.createStream({
                    streamID: uid,
                    audio: true,
                    cameraId: this.cameraId,
                    microphoneId: this.microphoneId,
                    video: true,
                    screen: false
                });
                streams[uid] = localStream;
                localStream.setVideoProfile('240P');
                // The user has granted access to the camera and mic.
                localStream.on("accessAllowed", () => {
                    this.logger("");
                    this.rtcCb({
                        name: 'accessAllowed',
                        data: {
                            code: this._code_enum_.MESSAGE_CODE_STREAM,
                        }
                    })
                });

                // The user has denied access to the camera and mic.
                localStream.on("accessDenied", () => {
                    this.logger("");
                    const msg = "";
                    this.rtcCb({
                        name: 'accessDenied',
                        data: {
                            msg,
                            code: this._code_enum_.MESSAGE_CODE_STREAM,
                        }
                    })
                    this.rtcCb({
                        name: 'error',
                        data: {
                            msg,
                            name:'onFirstLocalVideoFrame',
                            code: this._code_enum_.MESSAGE_CODE_STREAM,
                        }
                    })
                });

                localStream.init(() => {
                    this.logger("");
                    this.rtcCb({
                        name: 'onFirstLocalVideoFrame',
                        data: {
                            code: this._code_enum_.MESSAGE_CODE_STREAM,
                        }
                    })
                    //localStream.play('agora_local');
                    this.playById(uid)

                    client.publish(localStream, (err) => {
                        const msg = `: ${showErrorMsg(err)}`
                        this.logger(msg);
                        this.rtcCb({
                            name: 'error',
                            data: {
                                msg,
                                code: this._code_enum_.MESSAGE_CODE_STREAM,
                            }
                        })
                    });


                }, (err) => {
                    const msg = `: ${showErrorMsg(err)}`;
                    this.logger(msg);
                    this.rtcCb({
                        name: 'error',
                        data: {
                            msg,
                            name:'onFirstLocalVideoFrame',
                            code: this._code_enum_.MESSAGE_CODE_STREAM,
                        }
                    })
                });
            }
        }, (err) => {
            const msg = `: ${showErrorMsg(err)}`
            this.logger(msg);
            this.rtcCb({
                name: 'error',
                data: {
                    msg,
                    code: this._code_enum_.MESSAGE_CODE_ROOM,
                }
            })
        });
    }

    leaveRoom() {
        client && client.leave(() => {
            this.logger('')
            this.rtcCb({
                name: 'leave channel',
                data: {
                    code: this._code_enum_.MESSAGE_CODE_ROOM,
                }
            })


        }, (err) => {
            const msg = `${showErrorMsg(err)}`
            this.logger(msg)
            this.rtcCb({
                name: 'error',
                data: {
                    msg,
                    code: this._code_enum_.MESSAGE_CODE_ROOM,
                }
            })
        });
        const localStream = streams[this.uid];
        if (localStream) {
            localStream.stop();
            localStream.close();
            delete streams[this.uid]
        }

    }

    playById(id) {
        waitFor(() => streams[id] && this.document.getElementById(`${id}`))
            .then(() => {
                if (streams[id].isPlaying()) {
                    streams[id].stop();
                }
                streams[id].play(`${id}`)
                this.logger(`playById ${id}`);
            });
    }

    disableVideo({id, flag}) {
        this.logger(`disableVideo ${id} ${flag}`);
        const stream = streams[id];
        if(!stream)return;
        if (flag) {
            stream.enableVideo(id);
        } else {
            stream.disableVideo(id)
        }
    }

    disableAudio({id, flag}) {
        const stream = streams[id];
        if(!stream)return;
        if (flag) {
            stream.enableAudio();
        } else {
            stream.disableAudio();
        }
    }

    hideVideoById(id) {

    }

    showVideoById(id) {

    }

    _getRemoteVideoStats() {
        client && client.getRemoteVideoStats((remoteVideoStatsMap) => {

            for (var uid in remoteVideoStatsMap) {
                this.logger(remoteVideoStatsMap[uid])
                const {
                    RenderResolutionHeight,
                    RenderResolutionWidth,
                    RecvBitrate,
                    RenderFrameRate
                } = remoteVideoStatsMap[uid]
                const data ={
                    name :'RemoteVideoStats',
                    data:{
                        uid,
                        width: RenderResolutionWidth,                      // 
                        height: RenderResolutionHeight,                     // 
                        receivedBitrate: RecvBitrate,       // Kbps
                        receivedFrameRate: RenderFrameRate,
                        code: this._code_enum_.MESSAGE_CODE_STREAM,
                    }
                }
                this.rtcCb(data);
            }
        });
    }
    _getLocalVideoStats(){
        client&&client.getLocalVideoStats((localVideoStats) => {
            for(var uid in localVideoStats){
                const {
                    SendBitrate,
                    SendFrameRate,
                } = localVideoStats[uid]
                const data ={
                    name :'LocalVideoStats',
                    data:{
                        uid,
                        sentBitrate: SendBitrate,
                        sentFrameRate: SendFrameRate,
                        code: this._code_enum_.MESSAGE_CODE_STREAM,
                    }
                }
                this.rtcCb(data);

            }
        })
    }

    switchDevice(deviceType,deviceId){
        const stream = streams[this.uid];
        if(stream){
            stream.switchDevice(deviceType,deviceId)
        }
    }



}