import {RtcKit} from "./rtcKit";

import {getRoleType, waitFor} from "./util";
import {APP_ID, VIDEO_CHANEL_AGORA, VIDEO_CHANEL_ZEGO} from "./constants";


export class ElectronAgora extends RtcKit {
    constructor(options) {
        super(options)
        this.rtcName = 'electron agora';
        this.loggerColor = 'darkyellow';
        this.isSupportBeauty = false;
        this._getSupportBeaty_ = this._getSupportBeaty_.bind(this);
        this._initModule_ = this._initModule_.bind(this);
        this._isFirstLocalFrameInited_ = false;
        this.getChanelType = this.getChanelType.bind(this);
        this.avsEngine = null;
        this.ZmAvsSDK = null;
        this.APP_ID = APP_ID;
        this.appSign = '';
        
        
    }
    getChanelType(){
        switch (this.channel) {
            case VIDEO_CHANEL_AGORA:
                return this.ZmAvsSDK.ChannelType.CHANNEL_AGORA
            case VIDEO_CHANEL_ZEGO:
                return this.ZmAvsSDK.ChannelType.CHANNEL_ZEGO   
            default:
                return this.ZmAvsSDK.ChannelType.CHANNEL_AGORA
        }
    }
    _initModule_(){
        // 
        const channelType = this.getChanelType(); // 
        console.log('channelType',channelType)
        // 
        this.avsEngine.initialize(this.APP_ID, this.appSign, channelType);
        const width = 320;
        const height = 240;
        const fps = 15;
        const bitrate = 0;
        const minBitrate = -1;
        const orientationMode = this.ZmAvsSDK.OrientationMode.ORIENTATION_MODE_FIXED_LANDSCAPE;
        this.avsEngine.setVideoEncoderConfiguration(width, height, fps, bitrate, minBitrate, orientationMode);
        this.avsEngine.on(this.ZmAvsSDK.RtcEvent.RTC_EVENT_FIRST_LOCAL_VIDEO_FRAME, (width, height, elapsed) => {
            console.log(this.ZmAvsSDK.RtcEvent.RTC_EVENT_FIRST_LOCAL_VIDEO_FRAME, width, height, elapsed);
            this._isFirstLocalFrameInited_ = true;
            this.rtcCb({
                name:'onFirstLocalVideoFrame',
                data: Object.assign({},{},{code:this._code_enum_.MESSAGE_CODE_STREAM})
            })
        });
        this.avsEngine.on(this.ZmAvsSDK.RtcEvent.RTC_EVENT_FIRST_REMOTE_VIDEO_DECODEED, (uid, width, height, elapsed) => {
            console.log(this.ZmAvsSDK.RtcEvent.RTC_EVENT_FIRST_REMOTE_VIDEO_DECODEED, uid, width, height, elapsed);
            this.rtcCb({
                name:'onFirstLocalVideoFrame',
                data: Object.assign({},{uid},{code:this._code_enum_.MESSAGE_CODE_STREAM})
            })
          });
        
    }

    init(avsEngine,ZmAvsSDK,classType=0) {
        this.avsEngine = avsEngine;
        this.ZmAvsSDK = ZmAvsSDK;
        if (this._client_) return;
        this.rtcCb({
            name: 'inited',
            data: {
                code: this._code_enum_.MESSAGE_CODE_INIT,
            }
        })
        this._initModule_()
        this.joinRoom(classType);
        this._client_ = {};
        setTimeout(()=>{
            if(!this._isFirstLocalFrameInited_ && !this.isWatcher){
                this.rtcCb({
                    name: 'error',
                    data: {
                        msg:'',
                        name:'onFirstLocalVideoFrame',
                        code: this._code_enum_.MESSAGE_CODE_STREAM,
                    }
                })
            }
        },10000)
    }

    joinRoom(classType) {
        const roleType = getRoleType(this.role, this.isWatcher);
        const roomId = this.roomId;
        const userId = this.uid;
        this.avsEngine.joinChannel(classType, roomId, '', roleType, userId);
    }

    leaveRoom() {
        this.avsEngine.leaveChannel()
    }

    playById(id) {
        waitFor(() => document.getElementById(`${id}`))
            .then(() => {
                const dom = document.getElementById(`${id}`);
                const key = this.uid === id?'local':id;
                this.avsEngine.initRender(key, dom);
                this.avsEngine.setupViewContentMode(key, 1);
                this.logger(`playById ${id}`);
            });
    }

    disableVideo({id, flag}) {
        if(id === this.uid){
            this.avsEngine.muteLocalVideo(flag)
        }else{
            this.avsEngine.muteRemoteVideo(id, flag);
        }
        
    }

    disableAudio({id, flag}) {
        if(id === this.uid){
            this.avsEngine.muteLocalAudio(flag)
        }else{
            this.avsEngine.muteRemoteAudio(id, flag);
        }
    }

    
    setFaceValue(value){
        //windowNativeMethod('FaceValue', [JSON.stringify({value})])
    }

    
    _getSupportBeaty_(){
        try {
           return false;
          } catch (e) {
            return false;
          }
    }


}