import {
    VIDEO_CHANEL_AGORA,
    VIDEO_CHANEL_TALKCLOUD,
    VIDEO_CHANEL_TENCENT1,
    VIDEO_CHANEL_TENCENT2,
    VIDEO_CHANEL_ZEGO,
    VIDEO_CHANEL_ZMRTC
} from "./constants";

export function waitFor(conditionFunction) {
    const poll = resolve => {
        if (conditionFunction()) resolve();
        else setTimeout(_ => poll(resolve), 200);
    }
    return new Promise(poll);
}

export function showErrorMsg(err = {}) {
    return err.reason || err.info || err.msg || ''
}

export function getRoleType(role, isWatcher) {
    if (isWatcher) return 1;
    switch (role) {
        case 'teacher':
            return 0;
        case 'student':
            return 2;
        case 'seller':
            return 0;
        case 'watcher':
            return 1;
        default:
            return 4;
    }
}

export function getChanelType(name) {
    switch (name) {
        case VIDEO_CHANEL_ZMRTC:
            return 4;
        case VIDEO_CHANEL_AGORA:
            return 1;
        case VIDEO_CHANEL_TENCENT1:
            return 2;
        case VIDEO_CHANEL_TALKCLOUD:
            return 5;
        case VIDEO_CHANEL_ZEGO:
            return 6;
        case VIDEO_CHANEL_TENCENT2:
            return 7;
        default:
            return 0;
    }
}

export function makeUid(mobile) {
    if (!mobile) return '';

    let uid = mobile % 1000000000;
    if (uid < 100000000) uid += 100000000;
    return uid;
}
