import {
    VIDEO_CHANEL_AGORA,
    VIDEO_CHANEL_TENCENT1,
    VIDEO_CHANEL_TENCENT2,
    VIDEO_CHANEL_ZEGO,
    VIDEO_CHANEL_ZMRTC,
} from './constants'
import {WebAgora} from './webAgora'
import {WebZmRtc} from './webZmrtc'
import {WinAgora} from './winAgora'
import {WinZego} from './winZego'
import {WinTencentOne} from './winTencentOne'
import {MacTencentOne} from './macTencentOne'
import {isMac, isWeb, isWin, isXp, hasMethod} from 'zmNativeBridge'
import {WinTencentTwo} from "./winTencentTwo";
import {WinZmRtc} from "./winZmRtc";
import {MacAgora} from "./macAgora";
import {ElectronAgora} from "./electronAgora";
import {ElectronZego} from './electronZego'
import { MobileAgora } from './mobileAgora'

export function makeChannelInstance(channel,kind='oldpc') {
    const isNativeSupport =  hasMethod('LeaveRoom')
    console.log('kind',kind);
    if(kind === 'oldpc'){
        switch (channel) {
            case VIDEO_CHANEL_AGORA:
                if (isWeb()) {
                    return WebAgora
                } else if (isWin() && !isXp() && isNativeSupport) {
                    return WinAgora
                } else if (isMac()) {
                    return MacAgora
                } else {
                    return WebAgora
                }
            case VIDEO_CHANEL_ZEGO:
                return WinZego
            case VIDEO_CHANEL_TENCENT2:
                return WinTencentTwo
            case VIDEO_CHANEL_TENCENT1:
                if(isWin()){
                    return WinTencentOne
                }else if(isMac()){
                    return MacTencentOne
                }else{
                    return WinTencentOne
                }
    
            case VIDEO_CHANEL_ZMRTC:
                if (isWeb()) {
                    return WebZmRtc;
                } else if (isMac()) {
                    return WebZmRtc;
                } else if (isXp()) {
                    return WebZmRtc;
                } else if (isWin() && isNativeSupport) {
                    return WinZmRtc
                } else {
                    return WebZmRtc;
                }
            default:
                return WebAgora;
    
        }
    }else if(kind === 'newpc'){
        switch (channel) {
            case VIDEO_CHANEL_AGORA:
                return ElectronAgora;
            case VIDEO_CHANEL_ZEGO:
                return ElectronZego;    
            default:
                return ElectronAgora;    
        }
    }else if(kind === 'mobile'){
        switch (channel) {
            case VIDEO_CHANEL_AGORA:
                return MobileAgora;
            default:
                return MobileAgora
        }
    }
    
}