import {RtcKit} from "./rtcKit";
import {getCurrentWin, nativeOff, nativeOn, windowNativeMethod, focusCurrentWindow, windowNativeMethodSync} from "zmNativeBridge";
import {getChanelType, getRoleType} from "./util";


export class WinAgora extends RtcKit {
    constructor(options) {
        super(options)
        this.rtcName = 'window agora';
        this.loggerColor = 'blue';
        this.isSupportBeauty = true;
        this._uids_ = [];
        this._timeId_ = null;
        this._focTimeId_ = null;
        this._hiddenUids_ = [];
        this._remoteJoin_ = this._remoteJoin_.bind(this);
        this._moveFunc_ = this._moveFunc_.bind(this);
        this._moveById_ = this._moveById_.bind(this);
        this._getSupportBeaty_ = this._getSupportBeaty_.bind(this);
        this._onFirstLocalVideoFrame_ = this._onFirstLocalVideoFrame_.bind(this);
        this._initModule_ = this._initModule_.bind(this);
        this._callBackEvents_ = ['RemoteJoin','RemoteLeave','onFirstLocalVideoFrame',
            'UpdateDevice','agora speech analysis',
            'deviceChange','AudioVolumeChange','volumn level',
            'WinDoubleClick','onFirstLocalAudioFrame','CurrentSystemData',
            'mute_remote_video','rtcStatus','ConnectionLost'
        ];
        this._eventCallBackLogic_ = this._eventCallBackLogic_.bind(this);
        this._isFirstLocalFrameInited_ = false;

        // this._callBackEvents_ = ['RemoteJoin','RemoteLeave','onFirstLocalVideoFrame',
        //     'start push stream','device info','UpdateDevice','writingPad','agora speech analysis',
        //     'RobotPen Pos','deviceChange','AudioVolumeChange','tencent room create','volumn level',
        //     'tencent join room succ','WinDoubleClick','onFirstLocalAudioFrame','CurrentSystemData',
        //     'mute_remote_video','rtcStatus','ConnectionLost','RobotPen Mode'
        // ]

    }

    _eventCallBackLogic_(data){
        const { MsgType } = data
        switch (MsgType) {
            case 'RemoteJoin':
                this._remoteJoin_(data);
                break;
            case 'onFirstLocalVideoFrame':
                this._isFirstLocalFrameInited_ = true;
                this._onFirstLocalVideoFrame_(data);
                break;
            default:
                break;
        }

        this.rtcCb({
            name:MsgType,
            data: Object.assign({},data,{code:this._code_enum_.MESSAGE_CODE_STREAM})
        })
        this.logger('_eventCallBackLogic_',data,performance.now());

    }

    _moveFunc_() {
        if (this._timeId_) {
            clearTimeout(this._timeId_)
            this._timeId_ = null;
        }
        this._timeId_ = setTimeout(() => {
            this._uids_.filter((id) => !this._hiddenUids_.includes(id)).forEach((id) => {
                this._moveById_(id);
            })
        }, 200)
    }

    _remoteJoin_(data) {
        const uid = data.uid
        this._updateUids_(uid);
        windowNativeMethod('showRemoteVideoWin', [-1000, -1000, 0, 0, uid]);
        this._moveFunc_();
    }

    _updateUids_(id, kind = 'add') {
        id = `${id}`
        if (kind === 'add') {
            if (!this._uids_.includes(id)) {
                this._uids_.push(id);
            }
        } else if (kind === 'miu') {
            const index = this._uids_.indexOf(id);
            this._uids_.splice(index, 1);
        }

    }

    _updateHidderUids_(id, kind = 'add') {
        id = `${id}`
        if (kind === 'add') {
            if (!this._hiddenUids_.includes(id)) {
                this._hiddenUids_.push(id)
            }
        } else if (kind === 'miu') {
            if(this._hiddenUids_.includes(id)){
                const index = this._hiddenUids_.indexOf(id);
                this._hiddenUids_.splice(index, 1);
            }
        }

    }

    _onFirstLocalVideoFrame_() {
        this._moveFunc_();
    }

    _initModule_(){
        const cWin = getCurrentWin();
        const myView = cWin.getNativeWindowHandle();
        windowNativeMethod('setClassRoomWinHandler', [myView]);
    }

    init() {
        if (this._client_) return;
        this._updateUids_(this.uid);
        const cWin = getCurrentWin();
        this._initModule_();
        cWin.removeListener('move', this._moveFunc_)
        cWin.on('move', this._moveFunc_);
        const len = this._callBackEvents_.length
        for(let i = 0;i<len;i++){
            //nativeOff(this._callBackEvents_[i],this._eventCallBackLogic_)
            nativeOn(this._callBackEvents_[i],this._eventCallBackLogic_)
        }
        this.logger('this is inited')
        this.rtcCb({
            name: 'inited',
            data: {
                code: this._code_enum_.MESSAGE_CODE_INIT,
            }
        })
        this.joinRoom();
        this._client_ = {};
        setTimeout(()=>{
            if(!this._isFirstLocalFrameInited_ && !this.isWatcher){
                this.rtcCb({
                    name: 'error',
                    data: {
                        msg:'',
                        name:'onFirstLocalVideoFrame',
                        code: this._code_enum_.MESSAGE_CODE_STREAM,
                    }
                })
            }
        },10000)
    }

    joinRoom() {
        const roleType = getRoleType(this.role, this.isWatcher);
        const classType = 0; //11
        const channelType = getChanelType(this.channel);
        const roomId = this.roomId;
        const userId = this.uid;
        const lessonId = this.roomId;
        const isFace = this.isFace;
        const duration = this.duration;
        const mobile = this.mobile;
        const bu = '';
        const data = {
            roleType,
            classType,
            channelType,
            roomId,
            userId,
            duration,
            isFace,
            mobile,
            lessonId,
            bu,
        };
        windowNativeMethod('EnterRoom', [JSON.stringify(data)]);
        windowNativeMethod('moveVideoWin', [0, 0, 0, 0, userId]);
    }

    leaveRoom() {
        windowNativeMethod('LeaveRoom', ['LeaveRoom']);
    }

    playById(id) {

    }

    disableVideo({id, flag}) {
        windowNativeMethod('MuteVideo', [JSON.stringify({
            "mute": flag,
            "uid": id
        })]);
    }

    disableAudio({id, flag}) {
        windowNativeMethod('MuteVoice', [JSON.stringify({
            "mute": flag,
            "uid": id
        })]);
    }

    hideVideoById(id) {
        id = `${id}`
        windowNativeMethod('moveVideoWin', [0, 0, 0, 0, id]);
        this._updateHidderUids_(id, 'add')

    }

    showVideoById(id) {
        this._moveById_(id)
        this._updateHidderUids_(id, 'miu')


    }
    setFaceValue(value){
        windowNativeMethod('FaceValue', [JSON.stringify({value})])
    }

    _moveById_(id) {
        id = `${id}`
        const win = getCurrentWin();
        const {x, y} = win.getContentBounds();
        const scale = window.devicePixelRatio;
        const dom = this.document.getElementById(id);
        if (dom) {
            const bound = dom.getBoundingClientRect()
            const x1 = parseInt((x + bound.left) * scale);
            const y1 = parseInt((y + bound.top) * scale);
            const w = parseInt(bound.width * scale);
            const h = parseInt(bound.height * scale);
            this.logger(`window move ${x1},${y1}, ${w}, ${h}, ${id}`);
            windowNativeMethod('moveVideoWin', [x1, y1, w, h, id]);
            if(this._focTimeId_){
                clearTimeout(this._focTimeId_);
                this._focTimeId_ = null;
            }
            this._focTimeId_ = setTimeout(()=>{
                if(!win.isMinimized())focusCurrentWindow();
            },500)
        }
    }
    _getSupportBeaty_(){
        try {
            const nativeBeaty = windowNativeMethodSync('getFaceInfo', [
              JSON.stringify({
                courseType:1
              })
            ]);
            if (nativeBeaty !== '-1') {
              const data = JSON.parse(nativeBeaty).datas;
              return data.includes(this.channel)
            }else{
                 return false;
            }
          } catch (e) {
            return false;
          }
    }


}