import {WinAgora} from "./winAgora";
import {getChanelType, getRoleType} from "./util";
import {nativeOff, nativeOn, windowNativeMethod} from "zmNativeBridge";

export class WinTencentOne extends WinAgora {
    constructor(options) {
        super(options)
        const {tencentSign} = options;
        this.rtcName = 'window tencent1';
        this.loggerColor = 'lightpink';
        this.isSupportBeauty = false;
        this.tencentSign = tencentSign;
        this._onTencentRoomCreate_ = this._onTencentRoomCreate_.bind(this);
        this._onTencentJoinRoomSucc_ = this._onTencentJoinRoomSucc_.bind(this);
        this.tencentRoomCreate = this.tencentRoomCreate.bind(this);
        this.pushTencentStream = this.pushTencentStream.bind(this);
        this.createRoom = this.createRoom.bind(this);
    }

    _onTencentRoomCreate_(data){
        this.rtcCb({name:'tencent room create',data})
    }
    _onTencentJoinRoomSucc_(data){
        this.rtcCb({name:'tencent join room succ',data:{}})
    }
    createRoom(roomId){
        windowNativeMethod('OnTencentRoomNum', [`${roomId}`]);
    }
    tencentRoomCreate(data){
        windowNativeMethod('OnTencentRoomCreate', [data.roomId]);
    }
    pushTencentStream(){
        windowNativeMethod('pushTencentStream', [])
    }
    init(){
        super.init();
        nativeOff('tencent room create', this._onTencentRoomCreate_)
        nativeOn('tencent room create', this._onTencentRoomCreate_)
        nativeOff('tencent join room succ', this._onTencentJoinRoomSucc_)
        nativeOn('tencent join room succ', this._onTencentJoinRoomSucc_)
        this._isFirstLocalFrameInited_ = true;
    }

    joinRoom(){
        const roleType = getRoleType(this.role, this.isWatcher);
        const classType = 0; //11
        const channelType = getChanelType(this.channel);
        const roomId = this.roomId;
        const userId = this.uid;
        const lessonId = this.lessonId;
        const isFace = 0;
        const duration = this.duration;
        const mobile = this.mobile;
        const tencentSign = this.tencentSign;
        const bu = '';
        const data = {
            roleType,
            classType,
            channelType,
            roomId,
            userId,
            duration,
            isFace,
            mobile,
            lessonId,
            tencentSign,
            bu,
        };
        windowNativeMethod('EnterRoom', [JSON.stringify(data)]);
        windowNativeMethod('moveVideoWin', [0, 0, 0, 0, userId]);
        this.rtcCb({
            name: 'onFirstLocalVideoFrame',
            data: {
                code: this._code_enum_.MESSAGE_CODE_STREAM,
            }
        })
    }
}