import {RtcKit} from "./rtcKit";
import {waitFor} from "./util";
import './libs/erizo.js';

const AgoraRTC = require('./libs/AgoraRTCSDK-2.6.1');
let client = null;
let streams_map = {};


export class WebZmRtc extends RtcKit {
    constructor(options) {
        super(options)
        this.rtcName = 'web zmrtc';
        this.loggerColor = 'darkmagenta';
        this._subscribeToStreams_ = this._subscribeToStreams_.bind(this);

    }

    init() {
        if (!AgoraRTC.checkSystemRequirements()) {
            this.rtcCb({
                name: 'error',
                data: {
                    msg: 'WebRTC',
                    code: this._code_enum_.MESSAGE_CODE_INIT,
                }
            })
            return;
        }
        if (client) {
            this.rtcCb({
                name: 'error',
                data: {
                    msg: '',
                    code: this._code_enum_.MESSAGE_CODE_INIT,
                }
            })
            return;
        }

        const localCid = localStorage.getItem('ss.media.video_source_id');
        const localMid = localStorage.getItem('ss.media.audio_source_id');


        AgoraRTC.getDevices((devices) => {
            if (devices) {
                for (var i = 0; i < devices.length; i++) {
                    this.logger(`deviceId ${devices[i].deviceId}`);
                    if (devices[i].deviceId === localCid) this.cameraId = localCid;
                    if (devices[i].deviceId === localMid) this.microphoneId = localMid;
                }
            }

            this.logger('cameraId,microphoneId:', this.cameraId, this.microphoneId);

            // {
            //     optional : [{ sourceId: this.microphoneId }]
            // }

            const initConfig = {
                audio: true,
                video: true,
                data: true,
                videoSize: [320, 240, 320, 240],
                videoFrameRate: [10, 20],
            };
            const sOptions = Object.assign({}, initConfig, {attributes: {uid: `${this.uid}`}});
            console.log('sOptions', sOptions);

            const localStream = Erizo.Stream(sOptions);
            streams_map[this.uid] = localStream;
            client = Erizo.Room({token: this.roomId});

            client.addEventListener('room-connected', (roomEvent) => {
                this.logger('room-connected');
                this.rtcCb({
                    name: 'room joined',
                    data: {
                        code: this._code_enum_.MESSAGE_CODE_ROOM,
                    }
                })
                const options = {metadata: {type: 'publisher'}, attr: {hahah: 'true'}};
                // 
                !this.isWatcher && client.publish(localStream, options);
                this._subscribeToStreams_(roomEvent.streams);
            });

            // 
            client.addEventListener("room-disconnected", e => {
                this.logger('room-disconnected')
                this.rtcCb({
                    name: 'leave channel',
                    data: {
                        code: this._code_enum_.MESSAGE_CODE_ROOM,
                    }
                })

            });

            // 
            client.addEventListener('stream-subscribed', (streamEvent) => {
                this.logger('stream-subscribed', streamEvent);
                const stream = streamEvent.stream;
                const {mobile, uid} = stream.getAttributes() || {};
                streams_map[uid] = stream;
                this.playById(uid);
                this.rtcCb({
                    name: 'stream-subscribed',
                    data: {
                        uid,
                        code: this._code_enum_.MESSAGE_CODE_STREAM,
                    }
                })
            });

            // 
            client.addEventListener('stream-added', (streamEvent) => {
                this.logger('stream-added');
                this.rtcCb({
                    name: 'stream-added',
                    data: {
                        uid: streamEvent.stream && streamEvent.stream.getID(),
                        code: this._code_enum_.MESSAGE_CODE_STREAM,
                    }
                })
                this._subscribeToStreams_([streamEvent.stream]);
            });

            // 
            client.addEventListener('stream-removed', (streamEvent) => {
                this.logger('stream-removed', streamEvent.stream);
                const {mobile, uid} = streamEvent.stream.getAttributes() || {};
                this.rtcCb({
                    name: 'stream-removed',
                    data: {
                        uid,
                        code: this._code_enum_.MESSAGE_CODE_STREAM,
                    }
                })
            });

            // 
            client.addEventListener('stream-failed', () => {
                this.logger('stream-failed');
                this.rtcCb({
                    name: 'error',
                    data: {
                        msg: '',
                        code: this._code_enum_.MESSAGE_CODE_STREAM,
                    }
                })
            });

            // 
            localStream.addEventListener('access-accepted', (event) => {
                this.logger('access-accepted', event);
                client.connect();
                this.playById(this.uid);
                this.rtcCb({
                    name: 'accessAllowed',
                    data: {
                        code: this._code_enum_.MESSAGE_CODE_STREAM,
                    }
                })

            });

            localStream.addEventListener('access-denied', (event) => {
                this.logger('access-denied', event);
                client.connect();
                this.rtcCb({
                    name: 'accessDenied',
                    data: {
                        code: this._code_enum_.MESSAGE_CODE_STREAM,
                    }
                })
            });

            this.joinRoom();

        });

    }

    _subscribeToStreams_(_streams) {
        const streams = _streams.filter(item => item);
        this.logger('subscribe-streams', streams);
        for (let index in streams) {
            let stream = streams[index];
            if (streams_map[this.uid].getID() !== stream.getID()) {
                client.subscribe(stream, {
                    slideShowMode: false,
                    metadata: {type: 'subscriber'}
                });
                stream.addEventListener('bandwidth-alert', evt => this.logger(evt));
            } else {
                // addLocalVolumnLevelListener.bind(thisContext)(localStream);
                // socket.on('muteVoice', (data) => localStream.muteAudio(data.flag, noop));
                // appInfo.closeZmRtc = () => {
                //     room.disconnect();
                //     localStream.stop();
                //     localStream.close();
                //     initZmRtc._init = false;
                // }
            }
        }
    }

    joinRoom() {
        const userId = this.uid;
        const roomId = this.roomId;
        const isWatcher = this.isWatcher;
        this.logger(`:${userId} isWatcher:${!!isWatcher} :${roomId}...`);
        isWatcher ? client.connect() : streams_map[userId].init();
    }

    leaveRoom() {
        client && client.disconnect();
    }

    playById(id) {
        waitFor(() => streams_map[id] && this.document.getElementById(`${id}`))
            .then(() => {
                if (streams_map[id].showing) {
                    streams_map[id].stop();
                }
                streams_map[id].show(`${id}`)
            });
    }

    disableVideo({id, flag}) {
        const stream = streams_map[id];
        if (flag) {
            stream.muteVideo(false);
        } else {
            stream.muteVideo(true)
        }
    }

    disableAudio({id, flag}) {
        const stream = streams_map[id];
        if (flag) {
            stream.muteAudio();
        } else {
            stream.muteAudio();
        }
    }

    hideVideoById(id) {

    }

    showVideoById(id) {

    }


}