import {WinAgora} from "./winAgora";

export class WinZmRtc extends WinAgora {
    constructor(options) {
        super(options)
        this.rtcName = 'window zmrtc';
        this.loggerColor = 'lightcoral';
        this.isSupportBeauty = this._getSupportBeaty_();
    }
    init(){
        super.init();
        this.rtcCb({
            name: 'onFirstLocalVideoFrame',
            data: {
                code: this._code_enum_.MESSAGE_CODE_STREAM,
            }
        })
        this._isFirstLocalFrameInited_ = true;
    }
}